<template>
  <div
    class="todo__checkbox-wrap"
    :class="{ 'todo__checkbox-checked': check }"
    @click="handler"
  >
    <div class="todo__checkbox">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
        <path
          d="M173.898 439.404l-166.4-166.4c-9.997-9.997-9.997-26.206 0-36.204l36.203-36.204c9.997-9.998 26.207-9.998 36.204 0L192 312.69 432.095 72.596c9.997-9.997 26.207-9.997 36.204 0l36.203 36.204c9.997 9.997 9.997 26.206 0 36.204l-294.4 294.401c-9.998 9.997-26.207 9.997-36.204-.001z"
        ></path>
      </svg>
    </div>
    <div class="todo__checkbox-label">{{ label }}</div>
  </div>
</template>

<script>
export default {
  props: {
    label: { type: String, required: true },
    check: { type: Boolean, required: false },
  },
  data: () => ({
    checked: false,
  }),
  methods: {
    handler() {
      this.checked = !this.checked;
      this.$emit("change", this.checked);
    },
  },
};
</script>

<style lang="sass">
.todo__checkbox-wrap
  cursor: pointer
  user-select: none
  display: flex
  justify-content: start
  align-items: center
  opacity: 0.6
  transition: .25s
  height: 20px

.todo__checkbox
  width: 17px
  height: 17px
  border: 2px solid #69665C
  border-radius: 5px
  margin-right: 7px
  display: flex
  justify-content: center
  align-items: center
  overflow: hidden
  svg
    width: 90%
    height: auto
    fill: #69665C
    transition: .2s
    opacity: 0

.todo__checkbox-label
  font-size: 17px
  margin-bottom: -6px

.todo__checkbox-checked
  opacity: 1
  svg
    opacity: 1
</style>