<template>
  <div class="todo__menu">
    <ul class="todo__tags">
      <TodoTag
        v-for="(tag, index) in tags"
        :key="tag.created"
        :id="index"
        :bg="tag.bg"
        :title="tag.title"
        @select="select"
      />
    </ul>

    <TodoCheck
      @change="hideDoneTasks"
      @click="checked = !checked"
      :check="checked"
      label="Hide Done Tasks"
    />
  </div>
</template>

<script>
import TodoCheck from "./TodoCheck.vue";
import TodoTag from "./TodoTag.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Todo",
  components: { TodoCheck, TodoTag },
  data: () => ({
    idList: [],
    checked: false,
  }),

  methods: {
    ...mapActions(["toggleFilter", "hideDoneTasks"]),
    select({ id }) {
      this.toggleFilter(id);
    },
  },

  computed: {
    ...mapGetters(["tags"]),
  },
};
</script>

<style scoped lang="sass">
.todo__menu
  flex: 0 0 200px
  color: #69665C

.todo__tags
  margin: 0
  margin-bottom: 50px
  padding: 0

.todo__item
  list-style: none
  cursor: pointer
  display: flex
  justify-content: flex-start
  align-items: center
  border-radius: 15px
  font-size: 20px
  padding: 10px
  margin-bottom: 15px
  margin-left: -10px
  transition: .25s
  &:hover,
  &.active
    background-color: #F8F8F6

.todo__bg
  width: 33px
  height: 33px
  border-radius: 50%
  margin-right: 18px

.todo__label
  user-select: none
  cursor: pointer
  display: flex
  align-items: center
  justify-content: start
  font-family: roboto
  font-size: 18px

.todo__check
  width: 15px
  height: 15px
  border-radius: 5px
  border: 2px solid #69665C
  margin-right: 10px
  position: relative
  &::before
    content: ""
    position: absolute
    display: block
    border-left: 3px solid #69665C
    border-bottom: 3px solid #69665C
    width: 10px
    height: 3px
    // opacity: 0
  &.checked::before
    opacity: 1
</style>
