<template>
  <div class="todo-item" :class="{ 'todo-item__done': todo.done }">
    <div class="todo-item__top-panel">
      <h2 class="todo-item__heading">{{ todo.title }}</h2>
      <button class="todo-item__mnu-btn">
        <svg viewBox="0 0 331 93" xmlns="http://www.w3.org/2000/svg">
          <circle cx="46.5" cy="46.5" r="46.5" />
          <circle cx="284.5" cy="46.5" r="46.5" />
          <circle cx="165.5" cy="46.5" r="46.5" />
        </svg>
        <div class="todo-item__mnu">
          <ul>
            <li>
              <button @click="$emit('edit', todo.created)">Edit...</button>
            </li>
            <li><button @click="deleteTodo(todo.created)">Delete</button></li>
          </ul>
        </div>
      </button>
    </div>

    <div class="todo-item__description">
      <p>{{ todo.description }}</p>
    </div>

    <div class="todo-item__bottom-panel">
      <ul class="todo-item__tags-list">
        <li
          v-for="bg in tagsBg"
          :key="bg"
          :style="`background-color: ${bg}`"
          class="todo-item__tag"
        ></li>
      </ul>

      <TodoCheck
        @click="todoDoneToggle(todo.created)"
        :check="todo.done"
        label="Done"
      />
    </div>
  </div>
</template>

<script>
import TodoCheck from "./TodoCheck.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "TodoItem",
  props: {
    todo: { type: Object },
  },
  components: { TodoCheck },
  data: () => ({}),
  methods: { ...mapActions(["deleteTodo", "todoDoneToggle"]) },
  computed: {
    ...mapGetters(["tags"]),
    tagsBg() {
      const bg = [];
      this.todo.tags.forEach((id) => bg.push(this.tags[id].bg));
      return bg;
    },
  },
  emits: { edit: null },
};
</script>

<style lang="sass" scoped>
.todo-item
  background-color: #FFF9DC
  padding: 20px
  border-radius: 5px
  width: 24%
  min-width: 300px
  position: relative
  margin-bottom: 10px
  color: #69665C
  border: 2px solid transparent
  transition: background .2s
  &__done
    // background-color: #1DA362
    .todo-item__description,
    .todo-item__heading
      text-decoration: line-through !important
  &__top-panel
    display: flex
    justify-content: space-between
  &__heading
    word-wrap: break-word
    width: 90%
    margin: 0
  &__mnu-btn
    background-color: transparent
    width: 20px
    height: 20px
    border: 0
    outline: 0
    cursor: pointer
    padding: 0
    display: flex
    align-items: center
    position: relative
    &:hover
      .todo-item__mnu
        height: 90px
    svg
      fill: #A09E93
      width: 100%
      height: 100%
  &__mnu
    position: absolute
    background-color: #fff
    border-radius: 10px
    width: 150px
    height: 0
    transition: .2s
    overflow: hidden
    right: 0px
    top: 14px
    z-index: 9
    box-shadow: 2px 2px 10px rgba(#F4F5F2, .8)
    button
      border: 0
      outline: 0
      background-color: transparent
      padding: 15px
      width: 100%
      text-align: left
      color: #B2AFA1
      cursor: pointer
      transition: .2s
      &:hover
        background-color: #f4f5f2
    li
      &:first-child
        border-bottom: 1px solid rgba(#B2AFA1, .5)
  &__tags-list
    display: flex
    justify-content: start
    align-items: center
  &__tag
    width: 33px
    height: 33px
    border-radius: 50%
    margin-right: 10px
  &__bottom-panel
    display: flex
    justify-content: space-between
    align-items: center

.todo-item__description
  word-wrap: break-word
  width: 100%
</style>