<template>
  <div class="todo__editor">
    <div class="todo__buttons-panel">
      <button @click="$emit('cancel')" class="todo__cancel">Cancel</button>
      <button @click="addTodo" class="tobo__add hover-shadow">
        {{ edit ? "Edit" : "Add" }}
      </button>
    </div>

    <div class="todo__title-panel">
      <label class="todo__label">
        <div class="todo__title">Title</div>
        <input
          @keyup.enter="focusFromDesc"
          :maxlength="maxTitle"
          ref="title"
          v-model.trim="title"
          type="text"
          placeholder="add a title..."
        />
      </label>
      <div class="todo__count">{{ maxTitleLen }}/{{ maxTitle }}</div>
    </div>

    <div class="todo__content-panel">
      <label class="todo__label">
        <div class="todo__title">Description</div>
        <textarea
          ref="desc"
          v-model.trim="description"
          :maxlength="maxDescription"
          placeholder="add a description..."
        />
      </label>
      <div class="todo__count">
        {{ maxDescriptionLen }}/{{ maxDescription }}
      </div>
    </div>

    <div class="todo__tags-panel">
      <span class="todo__title">Tags</span>
      <ul class="todo__tags-list">
        <TodoTag
          v-for="(tag, index) in tags"
          :key="tag.created"
          :id="index"
          :bg="tag.bg"
          :title="tag.title"
          :tags="tagsList"
          @select="select"
        />
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import TodoTag from "./TodoTag.vue";

export default {
  name: "TodoEditor",
  components: { TodoTag },
  emits: { cancel: null },
  props: { itemId: { type: Number } },

  data: () => ({
    title: "",
    description: "",
    tagsList: [],
    maxTitle: 100,
    maxDescription: 600,
    edit: false,
    index: 0,
  }),

  methods: {
    ...mapActions(["addStateTodo", "editTodo"]),
    select({ id, selected }) {
      if (selected) this.tagsList.push(id);
      else this.tagsList.splice(this.tagsList.indexOf(id), 1);
    },

    addTodo() {
      if (!this.title && !this.description) return;
      if (this.edit) {
        this.editTodo({
          index: this.index,
          newTodo: {
            title: this.title,
            description: this.description,
            tags: this.tagsList,
          },
        });
        this.edit = false;
        this.$nextTick(this.$redrawVueMasonry);
      } else {
        this.addStateTodo({
          title: this.title,
          description: this.description,
          tags: this.tagsList,
        });
      }

      this.title = this.description = "";
      this.tagsList = [];
      this.$emit("cancel");
    },

    focusFromDesc() {
      this.$refs.desc.focus();
    },
  },

  computed: {
    ...mapGetters(["tags", "getTodos"]),
    maxTitleLen() {
      return this.maxTitle - this.title.length;
    },
    maxDescriptionLen() {
      return this.maxDescription - this.description.length;
    },
  },

  created() {
    if (!this.itemId) return;
    this.edit = true;
    this.getTodos.some((todo, index) => {
      if (todo.created === this.itemId) {
        this.title = todo.title;
        this.description = todo.description;
        this.tagsList = todo.tags.slice();
        this.index = index;
      }
    });
  },

  mounted() {
    const self = this;
    setTimeout(() => self.$refs.title.focus(), 300);
  },
};
</script>

<style lang="sass" scoped>
.todo__editor
  background-color: #fff
  width: 700px
  min-height: 500px
  opacity: 1
  z-index: 20
  position: absolute
  left: calc(50% - 350px)
  top: calc(50% - 250px)
  border-radius: 25px
  padding: 40px
  box-shadow: 4px 4px 5px rgba(0, 0, 0, .2)

.tobo__add
  border: 0
  outline: 0
  background-color: #69665C
  color: #fff
  padding: 10px 40px
  cursor: pointer
  border-radius: 8px

.todo__cancel
  border: 0
  outline: 0
  color: #69665C
  background-color: transparent
  cursor: pointer
  transition: .2s

.todo__buttons-panel
  display: flex
  justify-content: space-between
  align-items: center
  margin-bottom: 20px

.todo__label
  display: flex
  flex-direction: column
  input,
  textarea
    background-color: #F9F9F8
    border-radius: 8px
    border: 2px solid transparent
    transition: .2s
    outline: 0
    font-size: 16px !important
    padding: 10px
    color: #69665C
    &:focus
      border-color: rgba(#69665C , .7)
  textarea
    height: 100px
    resize: none
  input
    padding-right: 40px

.todo__tags-list
  display: flex
  justify-content: start
  align-items: center

.todo__title
  font-size: 27px
  font-weight: bold
  color: #69665C
  margin-bottom: 10px

.todo__title-panel,
.todo__content-panel
  margin-bottom: 30px
  position: relative
  display: flex
  flex-direction: column
  justify-content: flex-end

.todo__count
  font-size: 13px
  text-align: right
  padding-top: 7px
  color: #69665C
</style>