<template>
  <li
    class="todo__tag tag"
    :class="{ tag__selected: selected }"
    @click="handler"
  >
    <span :style="background" class="tag__bg"></span>
    <span class="tag__title">{{ title }}</span>
  </li>
</template>

<script>
export default {
  props: {
    id: { type: Number, required: true },
    bg: { type: String, required: true },
    title: { type: String, required: true },
    tags: { type: Array, required: false },
  },

  data: () => ({
    selected: false,
  }),

  methods: {
    handler() {
      this.selected = !this.selected;
      this.$emit("select", {
        selected: this.selected,
        id: this.id,
      });
    },
  },

  computed: {
    background() {
      return `background-color: ${this.bg}`;
    },
  },

  mounted() {
    if (this.tags && this.tags.indexOf(this.id) !== -1) this.selected = true;
  },
};
</script>

<style lang="sass" scoped>
.tag
  list-style: none
  cursor: pointer
  display: flex
  justify-content: flex-start
  align-items: center
  border-radius: 15px
  font-size: 20px
  padding: 10px
  margin-bottom: 15px
  margin-left: -10px
  transition: .25s
  margin-right: 20px
  color: #69665c
  &:hover,
  &__selected
    background-color: #F8F8F6

.tag__bg
  width: 33px
  height: 33px
  border-radius: 50%
  margin-right: 10px
</style>