<template>
  <div class="todo">
    <transition name="overlay">
      <div
        @click="overlayShow = false"
        v-if="overlayShow"
        class="todo__overlay"
      ></div>
    </transition>

    <transition name="editor">
      <TodoEditor
        @cancel="overlayShow = false"
        :itemId="created"
        v-if="overlayShow"
      />
    </transition>

    <div class="todo__top-line">
      <div class="todo__logo">todo</div>
      <button @click="callEditor(false)" class="todo__add-btn"></button>
    </div>
    <div class="todo__body">
      <TodoMenu />
      <div class="todo__content" ref="content">
        <div v-if="!isTodo" class="todo__start">
          <h1 class="todo__header">To-Do list is empty</h1>
          <button @click="callEditor(false)" class="todo__created hover-shadow">
            Create First To-Do
          </button>
        </div>

        <div
          v-masonry
          transition-duration=".3s"
          item-selector=".todo-item"
          gutter="10"
        >
          <TodoItem
            v-masonry-tile
            v-for="todo in todos"
            @edit="callEditor"
            :key="todo.created"
            :todo="todo"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TodoMenu from "./components/TodoMenu.vue";
import TodoEditor from "./components/TodoEditor.vue";
import TodoItem from "./components/TodoItem.vue";

import { mapGetters } from "vuex";

export default {
  components: {
    TodoMenu,
    TodoEditor,
    TodoItem,
  },
  data: () => ({
    overlayShow: false,
    created: 0,
  }),

  methods: {
    callEditor(id) {
      if (id) this.created = id;
      else this.created = 0;
      this.overlayShow = true;
    },
  },

  computed: { ...mapGetters(["isTodo", "todos"]) },
};
</script>

