// import { doc } from "prettier";
import { Object } from "core-js";
import { createStore } from "vuex";

const saveState = (store) => {
  const key = "AppTodos";
  const localVal = localStorage.getItem(key);
  if (localVal) store.commit("importTodos", JSON.parse(localVal));
  store.subscribe((mutation, state) => {
    localStorage.setItem(key, JSON.stringify(state.todos));
  });
};

export default createStore({
  state: {
    tags: [
      { created: "83274qw876", title: "work", bg: "#D2CEFF" },
      { created: "83274erw87", title: "study", bg: "#D1E5F7" },
      { created: "832asd7487", title: "entertainment", bg: "#FFCECE" },
      { created: "832bv74876", title: "family", bg: "#DAF2D6" },
    ],
    todos: [],
    filters: [],
    hideDoneTasks: false,
  },
  getters: {
    todos: (state) => {
      const todos = state.todos.filter((todo) =>
        state.hideDoneTasks ? !todo.done : true
      );
      if (state.filters.length === 0) return todos;
      else
        return todos.filter((todo) => {
          return state.filters.some((filter) => todo.tags.includes(filter));
        });
    },

    getTodos: (state) => state.todos,

    tags: (state) => state.tags,
    isTodo: (state) => state.todos.length > 0,
    tagsBg: (state, idList) => {
      const bgList = [];
      idList.forEach((id) => bgList.push(state.tags[id].bg));
      return bgList;
    },
  },
  mutations: {
    addStateTodo: (state, newTodo) => {
      state.todos.push({
        title: newTodo.title,
        description: newTodo.description,
        tags: newTodo.tags,
        created: Date.now(),
        done: false,
      });
    },

    editTodo: (state, obj) =>
      (state.todos[obj.index] = Object.assign(
        state.todos[obj.index],
        obj.newTodo
      )),

    deleteTodo: (state, created) => {
      state.todos.some((todo, index) => {
        if (todo.created === created) {
          state.todos.splice(index, 1);
          return true;
        }
      });
    },
    todoDoneToggle: (state, created) =>
      state.todos.some((todo) => {
        if (todo.created === created) {
          todo.done = !todo.done;
          return true;
        }
      }),
    toggleFilter: (state, id) => {
      const index = state.filters.indexOf(id);
      if (index === -1) state.filters.push(id);
      else state.filters.splice(index, 1);
    },
    importTodos: (state, todos) => (state.todos = todos),
    hideDoneTasks: (state, val) => (state.hideDoneTasks = val),
  },
  actions: {
    addStateTodo: ({ commit }, newTodo) => commit("addStateTodo", newTodo),
    deleteTodo: ({ commit }, index) => commit("deleteTodo", index),
    todoDoneToggle: ({ commit }, index) => commit("todoDoneToggle", index),
    toggleFilter: ({ commit }, id) => commit("toggleFilter", id),
    hideDoneTasks: ({ commit }, val) => commit("hideDoneTasks", val),
    editTodo: ({ commit }, obj) => commit("editTodo", obj),
  },

  plugins: [saveState],
});
